* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  text-align: center;
  color: #bcb4b4;
  font-size: 3rem;
  padding-top: 50px;
  padding-bottom: 50px;
}

.container {
  width: 1140px;
  margin: 0 auto;
}

.section_area_grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
}

.section_grid_item {
  color: #bcb4b4;
  padding-bottom: 30px;
}

.setion_item__img img {
  width: 100%;
}

.section_item_text {
  padding: 1px;
}

a {
  color: aqua;
}

@media only screen and(max-width: 480px) {
  .section_area_grid {
    grid-template-columns: 1fr;
  }
}
