.top-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding-top: 10px;
  padding-left: 50px;
  color: #bcb4b4;

  .left {
    flex: 0.5;

    .head {
      font-size: 3rem;
    }
    .ab {
      .about__subheading {
        font-size: 1.5rem;
        margin-bottom: 2rem;
        width: 100%;
        height: 100%;

        .item {
          background-color: #1e1e1e;
          tab-size: 4;
          border-radius: 8px;
        }
      }
    }
  }
}
