* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
  text-align: center;
  color: #bcb4b4;
  font-size: 3rem;
}

.container {
  width: 900px;
  margin: 0 auto;
  padding-top: 0;
}

.section_area_grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
}

.section_grid_item {
  text-align: center;
  color: #bcb4b4;
}

.setion_item__img img {
  width: 100%;
}

@media only screen and(max-width: 480px) {
  .section_area_grid {
    grid-template-columns: 1fr;
  }
}
